@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700;800&display=swap');

:root {
    --orange: #ff6b35;
    --orange-light: #ff8c5a;
    --orange-dark: #e55a2b;
    --blue: #1e3a5f;
    --blue-light: #2d5a8a;
    --blue-dark: #0f2137;
    --white: #ffffff;
    --gray-50: #f8fafc;
    --gray-100: #f1f5f9;
    --gray-200: #e2e8f0;
    --gray-600: #475569;
    --gray-800: #1e293b;
}

* { margin: 0; padding: 0; box-sizing: border-box; }
html { scroll-behavior: smooth; }

body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, sans-serif;
    line-height: 1.6;
    color: var(--gray-800);
    background-color: var(--white);
    overflow-x: hidden;
}

/* Animations */
@keyframes fadeInUp {
    from { opacity: 0; transform: translateY(40px); }
    to { opacity: 1; transform: translateY(0); }
}

@keyframes fadeInDown {
    from { opacity: 0; transform: translateY(-40px); }
    to { opacity: 1; transform: translateY(0); }
}

@keyframes fadeInLeft {
    from { opacity: 0; transform: translateX(-40px); }
    to { opacity: 1; transform: translateX(0); }
}

@keyframes fadeInRight {
    from { opacity: 0; transform: translateX(40px); }
    to { opacity: 1; transform: translateX(0); }
}

@keyframes scaleIn {
    from { opacity: 0; transform: scale(0.9); }
    to { opacity: 1; transform: scale(1); }
}

@keyframes pulse {
    0%, 100% { opacity: 1; transform: scale(1); }
    50% { opacity: 0.7; transform: scale(1.1); }
}

@keyframes float {
    0%, 100% { transform: translateY(0); }
    50% { transform: translateY(-15px); }
}

@keyframes gradientMove {
    0% { background-position: 0% 50%; }
    50% { background-position: 100% 50%; }
    100% { background-position: 0% 50%; }
}

@keyframes bounce {
    0%, 20%, 50%, 80%, 100% { transform: translateY(0); }
    40% { transform: translateY(-20px); }
    60% { transform: translateY(-10px); }
}

.animate-on-scroll {
    opacity: 0;
    transform: translateY(30px);
    transition: all 0.8s cubic-bezier(0.4, 0, 0.2, 1);
}

.animate-on-scroll.visible {
    opacity: 1;
    transform: translateY(0);
}

.animate-delay-1 { transition-delay: 0.1s; }
.animate-delay-2 { transition-delay: 0.2s; }
.animate-delay-3 { transition-delay: 0.3s; }
.animate-delay-4 { transition-delay: 0.4s; }

/* Header */
header {
    background: rgba(255, 255, 255, 0.98);
    backdrop-filter: blur(20px);
    -webkit-backdrop-filter: blur(20px);
    padding: 0.75rem 1.5rem;
    position: sticky;
    top: 0;
    z-index: 1000;
    border-bottom: 1px solid rgba(0,0,0,0.05);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

header.scrolled {
    padding: 0.5rem 1.5rem;
    box-shadow: 0 4px 30px rgba(0,0,0,0.1);
}

.header-content {
    max-width: 1400px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    display: flex;
    align-items: center;
    gap: 0.75rem;
    text-decoration: none;
    animation: fadeInLeft 0.8s ease-out;
}

.logo-img {
    height: 50px;
    width: auto;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.logo:hover .logo-img {
    transform: scale(1.05);
}

.logo-text h1 {
    font-size: 1.4rem;
    font-weight: 800;
    background: linear-gradient(135deg, var(--blue) 0%, var(--blue-light) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.logo-text span {
    display: block;
    font-size: 0.65rem;
    font-weight: 500;
    color: var(--gray-600);
}

/* Desktop Navigation */
nav {
    animation: fadeInDown 0.8s ease-out;
}

nav ul {
    display: flex;
    list-style: none;
    gap: 0.25rem;
}

nav ul li a {
    color: var(--gray-600);
    text-decoration: none;
    font-size: 0.9rem;
    font-weight: 500;
    padding: 0.6rem 1rem;
    border-radius: 8px;
    transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
    position: relative;
}

nav ul li a::before {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 0;
    height: 2px;
    background: var(--orange);
    transition: all 0.3s ease;
    transform: translateX(-50%);
}

nav ul li a:hover {
    color: var(--orange);
    background: rgba(255, 107, 53, 0.08);
}

nav ul li a:hover::before {
    width: 60%;
}

.header-cta {
    display: flex;
    align-items: center;
    gap: 1.5rem;
    animation: fadeInRight 0.8s ease-out;
}

.header-phones {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    gap: 0.1rem;
}

.phone-link {
    color: var(--blue);
    text-decoration: none;
    font-weight: 600;
    font-size: 0.85rem;
    transition: all 0.3s ease;
}

.phone-link:hover {
    color: var(--orange);
}

/* Mobile Menu Button */
.mobile-menu-btn {
    display: none;
    flex-direction: column;
    gap: 5px;
    padding: 8px;
    background: none;
    border: none;
    cursor: pointer;
    z-index: 1001;
}

.mobile-menu-btn span {
    width: 24px;
    height: 2px;
    background: var(--blue);
    border-radius: 2px;
    transition: all 0.3s ease;
}

.mobile-menu-btn.active span:nth-child(1) {
    transform: rotate(45deg) translate(5px, 5px);
}

.mobile-menu-btn.active span:nth-child(2) {
    opacity: 0;
}

.mobile-menu-btn.active span:nth-child(3) {
    transform: rotate(-45deg) translate(5px, -5px);
}

/* Mobile Navigation */
.mobile-nav {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background: rgba(255,255,255,0.98);
    backdrop-filter: blur(20px);
    z-index: 999;
    padding: 100px 2rem 2rem;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.mobile-nav.active {
    opacity: 1;
    visibility: visible;
}

.mobile-nav ul {
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
}

.mobile-nav ul li {
    transform: translateX(-30px);
    opacity: 0;
    transition: all 0.4s ease;
}

.mobile-nav.active ul li {
    transform: translateX(0);
    opacity: 1;
}

.mobile-nav.active ul li:nth-child(1) { transition-delay: 0.1s; }
.mobile-nav.active ul li:nth-child(2) { transition-delay: 0.15s; }
.mobile-nav.active ul li:nth-child(3) { transition-delay: 0.2s; }
.mobile-nav.active ul li:nth-child(4) { transition-delay: 0.25s; }
.mobile-nav.active ul li:nth-child(5) { transition-delay: 0.3s; }
.mobile-nav.active ul li:nth-child(6) { transition-delay: 0.35s; }

.mobile-nav ul li a {
    display: block;
    color: var(--blue);
    text-decoration: none;
    font-size: 1.5rem;
    font-weight: 700;
    padding: 1rem 0;
    border-bottom: 1px solid var(--gray-200);
    transition: all 0.3s ease;
}

.mobile-nav ul li a:hover {
    color: var(--orange);
    padding-left: 1rem;
}

/* Buttons */
.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, var(--orange) 0%, var(--orange-light) 100%);
    background-size: 200% 200%;
    color: white;
    padding: 0.85rem 1.75rem;
    border-radius: 12px;
    text-decoration: none;
    font-weight: 600;
    font-size: 0.95rem;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    border: none;
    cursor: pointer;
    box-shadow: 0 4px 15px rgba(255, 107, 53, 0.3);
    position: relative;
    overflow: hidden;
}

.btn::before {
    content: '';
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, transparent, rgba(255,255,255,0.3), transparent);
    transition: left 0.5s ease;
}

.btn:hover {
    transform: translateY(-3px);
    box-shadow: 0 12px 30px rgba(255, 107, 53, 0.4);
    animation: gradientMove 2s ease infinite;
}

.btn:hover::before {
    left: 100%;
}

.btn:disabled {
    opacity: 0.7;
    cursor: not-allowed;
    transform: none;
}

.btn-large {
    padding: 1.1rem 2.5rem;
    font-size: 1.05rem;
    border-radius: 14px;
}

.btn-outline {
    background: transparent;
    border: 2px solid white;
    box-shadow: none;
}

.btn-outline:hover {
    background: white;
    color: var(--blue);
}

/* Hero Section */
.hero {
    min-height: 100vh;
    display: flex;
    align-items: center;
    background: linear-gradient(135deg, var(--blue-dark) 0%, var(--blue) 50%, var(--blue-light) 100%);
    background-size: 200% 200%;
    animation: gradientMove 15s ease infinite;
    position: relative;
    overflow: hidden;
    padding: 4rem 1.5rem 3rem;
}

.hero::before {
    content: '';
    position: absolute;
    top: -50%;
    right: -20%;
    width: 80%;
    height: 150%;
    background: radial-gradient(circle, rgba(255,107,53,0.15) 0%, transparent 70%);
    animation: float 8s ease-in-out infinite;
}

.particle {
    position: absolute;
    width: 10px;
    height: 10px;
    background: rgba(255,107,53,0.3);
    border-radius: 50%;
    animation: float 6s ease-in-out infinite;
}

.particle:nth-child(1) { top: 20%; left: 10%; animation-delay: 0s; }
.particle:nth-child(2) { top: 60%; left: 20%; animation-delay: 1s; width: 15px; height: 15px; }
.particle:nth-child(3) { top: 30%; right: 15%; animation-delay: 2s; width: 8px; height: 8px; }
.particle:nth-child(4) { bottom: 20%; right: 25%; animation-delay: 3s; }
.particle:nth-child(5) { top: 70%; left: 60%; animation-delay: 4s; width: 12px; height: 12px; }

.hero-content {
    max-width: 1400px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    align-items: center;
    position: relative;
    z-index: 1;
}

.hero-text { color: white; }

.hero-badge {
    display: inline-flex;
    align-items: center;
    gap: 0.5rem;
    background: rgba(255,255,255,0.1);
    backdrop-filter: blur(10px);
    padding: 0.5rem 1rem;
    border-radius: 50px;
    font-size: 0.8rem;
    font-weight: 500;
    margin-bottom: 1.5rem;
    border: 1px solid rgba(255,255,255,0.1);
}

.hero-badge span {
    width: 8px;
    height: 8px;
    background: var(--orange);
    border-radius: 50%;
    animation: pulse 2s infinite;
}

.hero h2 {
    font-size: clamp(2rem, 5vw, 3.5rem);
    font-weight: 800;
    line-height: 1.1;
    margin-bottom: 1.5rem;
}

.hero h2 .highlight {
    background: linear-gradient(135deg, var(--orange) 0%, var(--orange-light) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.hero p {
    font-size: clamp(1rem, 2vw, 1.2rem);
    line-height: 1.8;
    opacity: 0.9;
    margin-bottom: 2rem;
    max-width: 540px;
}

.hero-buttons {
    display: flex;
    gap: 1rem;
    flex-wrap: wrap;
}

.hero-card {
    background: rgba(255,255,255,0.1);
    backdrop-filter: blur(20px);
    border-radius: 24px;
    padding: 2rem;
    border: 1px solid rgba(255,255,255,0.1);
    transition: all 0.4s ease;
}

.hero-card:hover {
    transform: translateY(-10px);
    box-shadow: 0 30px 60px rgba(0,0,0,0.2);
}

.hero-stats {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1.5rem;
}

.stat-item {
    text-align: center;
    color: white;
    transition: all 0.3s ease;
}

.stat-item:hover {
    transform: scale(1.05);
}

.stat-item .number {
    font-size: clamp(1.8rem, 4vw, 2.5rem);
    font-weight: 800;
    color: var(--orange);
}

.stat-item .label {
    font-size: 0.8rem;
    opacity: 0.8;
    margin-top: 0.25rem;
}

.scroll-indicator {
    position: absolute;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.5rem;
    color: white;
    opacity: 0.7;
    animation: bounce 2s infinite;
}

.scroll-indicator span {
    font-size: 0.75rem;
    text-transform: uppercase;
    letter-spacing: 2px;
}

.scroll-indicator .arrow {
    width: 24px;
    height: 24px;
    border-right: 2px solid white;
    border-bottom: 2px solid white;
    transform: rotate(45deg);
}

/* Sections */
section {
    padding: 4rem 1.5rem;
}

.section-container {
    max-width: 1400px;
    margin: 0 auto;
}

.section-header {
    text-align: center;
    margin-bottom: 3rem;
}

.section-tag {
    display: inline-block;
    background: linear-gradient(135deg, rgba(255,107,53,0.1) 0%, rgba(255,107,53,0.05) 100%);
    color: var(--orange);
    padding: 0.5rem 1.25rem;
    border-radius: 50px;
    font-size: 0.8rem;
    font-weight: 600;
    margin-bottom: 1rem;
    text-transform: uppercase;
    letter-spacing: 1px;
}

.section-title {
    font-size: clamp(1.75rem, 4vw, 2.5rem);
    font-weight: 800;
    color: var(--blue);
    margin-bottom: 1rem;
}

.section-subtitle {
    font-size: clamp(0.95rem, 2vw, 1.1rem);
    color: var(--gray-600);
    max-width: 600px;
    margin: 0 auto;
}

/* Catalogue */
.catalogue { background: var(--gray-50); }

.category-tabs {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    justify-content: center;
    margin-bottom: 2rem;
    padding: 0 0.5rem;
}

.category-tab {
    padding: 0.65rem 1.25rem;
    border: 2px solid var(--gray-200);
    border-radius: 50px;
    background: white;
    color: var(--gray-600);
    font-weight: 600;
    font-size: 0.85rem;
    cursor: pointer;
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    white-space: nowrap;
}

.category-tab:hover {
    border-color: var(--orange);
    color: var(--orange);
    transform: translateY(-2px);
}

.category-tab.active {
    background: linear-gradient(135deg, var(--orange) 0%, var(--orange-light) 100%);
    border-color: var(--orange);
    color: white;
    transform: scale(1.05);
    box-shadow: 0 8px 20px rgba(255,107,53,0.3);
}

.catalogue-disclaimer {
    background: linear-gradient(135deg, rgba(30, 58, 95, 0.05) 0%, rgba(30, 58, 95, 0.02) 100%);
    border-left: 4px solid var(--blue);
    padding: 1rem 1.25rem;
    margin-bottom: 2rem;
    border-radius: 0 12px 12px 0;
    font-size: 0.85rem;
    color: var(--gray-600);
    line-height: 1.7;
}

.catalogue-disclaimer strong {
    color: var(--blue);
}

.products-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
    gap: 1.5rem;
}

.product-card {
    background: white;
    border-radius: 20px;
    overflow: hidden;
    transition: all 0.5s cubic-bezier(0.4, 0, 0.2, 1);
    border: 1px solid var(--gray-200);
}

.product-card.show {
    opacity: 1;
    transform: translateY(0) scale(1);
}

.product-card:hover {
    transform: translateY(-12px) scale(1.02);
    box-shadow: 0 25px 50px rgba(0,0,0,0.15);
    border-color: var(--orange);
}

.product-image-wrapper {
    position: relative;
    overflow: hidden;
}

.product-image {
    width: 100%;
    height: 200px;
    object-fit: cover;
    background: var(--gray-100);
    transition: all 0.5s ease;
}

.product-card:hover .product-image {
    transform: scale(1.1);
}

.product-info {
    padding: 1.25rem;
}

.product-art {
    font-size: 0.75rem;
    color: var(--gray-600);
    margin-bottom: 0.25rem;
}

.product-name {
    font-size: 1rem;
    font-weight: 600;
    color: var(--blue);
    margin-bottom: 0.5rem;
    transition: color 0.3s ease;
}

.product-card:hover .product-name {
    color: var(--orange);
}

.product-size {
    font-size: 0.85rem;
    color: var(--gray-600);
    margin-bottom: 0.75rem;
}

.product-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.product-price {
    font-size: 1.1rem;
    font-weight: 700;
    color: var(--orange);
}

.product-btn {
    padding: 0.5rem 1rem;
    font-size: 0.8rem;
    border-radius: 8px;
}

/* Benefits */
.benefits { background: white; }

.benefits-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1.5rem;
}

.benefit-card {
    background: linear-gradient(135deg, var(--blue) 0%, var(--blue-light) 100%);
    border-radius: 16px;
    padding: 1.75rem;
    color: white;
    transition: all 0.3s ease;
}

.benefit-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 35px rgba(30, 58, 95, 0.25);
}

.benefit-number {
    font-size: 2.5rem;
    font-weight: 800;
    color: var(--orange);
    opacity: 0.9;
    margin-bottom: 0.75rem;
}

.benefit-card h3 {
    font-size: 1.05rem;
    font-weight: 700;
    margin-bottom: 0.5rem;
}

.benefit-card p {
    font-size: 0.85rem;
    opacity: 0.85;
    line-height: 1.5;
}

/* About */
.about {
    background: linear-gradient(135deg, var(--blue-dark) 0%, var(--blue) 100%);
    color: white;
    position: relative;
    overflow: hidden;
}

.about::before {
    content: '';
    position: absolute;
    top: -50%;
    right: -30%;
    width: 80%;
    height: 150%;
    background: radial-gradient(circle, rgba(255,107,53,0.1) 0%, transparent 70%);
    animation: float 12s ease-in-out infinite;
}

.about .section-tag { background: rgba(255,107,53,0.2); color: var(--orange); }
.about .section-title,
.about .section-subtitle { color: white; }

.about-content {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 3rem;
    align-items: center;
    position: relative;
    z-index: 1;
}

.about-text {
    font-size: 1.05rem;
    line-height: 1.9;
    opacity: 0.9;
}

.about-features {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 1rem;
}

.about-feature {
    background: rgba(255,255,255,0.08);
    backdrop-filter: blur(10px);
    border-radius: 16px;
    padding: 1.25rem;
    border: 1px solid rgba(255,255,255,0.1);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.about-feature:hover {
    background: rgba(255,255,255,0.15);
    transform: translateY(-8px) scale(1.02);
    box-shadow: 0 20px 40px rgba(0,0,0,0.2);
}

.about-feature .icon {
    width: 48px;
    height: 48px;
    background: linear-gradient(135deg, var(--orange) 0%, var(--orange-light) 100%);
    border-radius: 12px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.5rem;
    margin-bottom: 1rem;
    transition: all 0.3s ease;
}

.about-feature:hover .icon {
    transform: rotate(10deg) scale(1.1);
}

.about-feature h4 {
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 0.5rem;
}

.about-feature p {
    font-size: 0.85rem;
    opacity: 0.7;
}

/* Characteristics */
.char-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 1rem;
}

.char-card {
    background: var(--gray-50);
    border-radius: 16px;
    padding: 1.25rem;
    border: 1px solid var(--gray-200);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    text-align: center;
}

.char-card:hover {
    border-color: var(--orange);
    box-shadow: 0 15px 35px rgba(255,107,53,0.15);
    transform: translateY(-8px);
}

.char-card .value {
    font-size: 1.25rem;
    font-weight: 800;
    color: var(--orange);
    margin-bottom: 0.5rem;
}

.char-card .label {
    font-size: 0.85rem;
    color: var(--gray-600);
}

/* Contacts */
.contacts-grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 1.5rem;
}

.contact-card {
    background: var(--gray-50);
    border-radius: 20px;
    padding: 2rem;
    text-align: center;
    border: 1px solid var(--gray-200);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

.contact-card:hover {
    border-color: var(--orange);
    box-shadow: 0 20px 45px rgba(255,107,53,0.15);
    transform: translateY(-10px);
}

.contact-card .icon {
    width: 60px;
    height: 60px;
    background: linear-gradient(135deg, var(--orange) 0%, var(--orange-light) 100%);
    border-radius: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.75rem;
    margin: 0 auto 1.25rem;
    transition: all 0.3s ease;
}

.contact-card:hover .icon {
    transform: rotate(10deg) scale(1.1);
}

.contact-card h3 {
    font-size: 1.15rem;
    font-weight: 700;
    color: var(--blue);
    margin-bottom: 0.75rem;
}

.contact-card p,
.contact-card address {
    font-size: 0.95rem;
    color: var(--gray-600);
    line-height: 1.7;
    font-style: normal;
}

.contact-card a {
    color: var(--blue);
    text-decoration: none;
    transition: color 0.3s ease;
}

.contact-card a:hover {
    color: var(--orange);
}

.map-links {
    display: flex;
    gap: 0.75rem;
    justify-content: center;
    margin-top: 1rem;
}

.map-link {
    display: inline-flex;
    align-items: center;
    gap: 0.4rem;
    padding: 0.5rem 1rem;
    border-radius: 8px;
    font-size: 0.8rem;
    font-weight: 600;
    text-decoration: none;
    transition: all 0.3s ease;
}

.map-link.yandex {
    background: rgba(255, 204, 0, 0.15);
    color: #fc0;
}

.map-link.yandex:hover {
    background: #fc0;
    color: #333;
}

.map-link.google {
    background: rgba(66, 133, 244, 0.15);
    color: #4285f4;
}

.map-link.google:hover {
    background: #4285f4;
    color: white;
}

.map-link span {
    font-weight: 800;
}

/* Contact Form */
.contact-form {
    background: linear-gradient(135deg, var(--gray-50) 0%, white 100%);
}

.form-wrapper {
    display: grid;
    grid-template-columns: 1fr 1.5fr;
    gap: 2rem;
    align-items: start;
}

.form-info {
    background: linear-gradient(135deg, var(--blue) 0%, var(--blue-light) 100%);
    border-radius: 20px;
    padding: 2rem;
    color: white;
}

.form-info h3 {
    font-size: 1.25rem;
    font-weight: 700;
    margin-bottom: 1.5rem;
}

.form-info-item {
    display: flex;
    align-items: flex-start;
    gap: 1rem;
    margin-bottom: 1.25rem;
}

.form-info-item .icon {
    width: 40px;
    height: 40px;
    background: rgba(255,255,255,0.15);
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    flex-shrink: 0;
}

.form-info-item p {
    font-size: 0.95rem;
    line-height: 1.6;
    opacity: 0.9;
}

.form-info-item .label-text {
    font-size: 0.75rem;
    opacity: 0.7;
    display: block;
    margin-bottom: 0.25rem;
}

.form-info-item a {
    color: white;
    text-decoration: none;
}

.form-info-item a:hover {
    text-decoration: underline;
}

.form-container {
    background: white;
    border-radius: 20px;
    padding: 2rem;
    box-shadow: 0 10px 40px rgba(0,0,0,0.08);
}

.form-group {
    margin-bottom: 1.25rem;
}

.form-group label {
    display: block;
    font-size: 0.9rem;
    font-weight: 600;
    color: var(--gray-800);
    margin-bottom: 0.5rem;
}

.form-group input,
.form-group textarea {
    width: 100%;
    padding: 0.9rem 1rem;
    border: 2px solid var(--gray-200);
    border-radius: 12px;
    font-size: 0.95rem;
    font-family: inherit;
    transition: all 0.3s ease;
    background: var(--gray-50);
}

.form-group input:focus,
.form-group textarea:focus {
    outline: none;
    border-color: var(--orange);
    background: white;
    box-shadow: 0 0 0 4px rgba(255, 107, 53, 0.1);
}

.phone-input-wrapper {
    display: flex;
    align-items: center;
    border: 2px solid var(--gray-200);
    border-radius: 12px;
    overflow: hidden;
    transition: all 0.3s ease;
    background: var(--gray-50);
}

.phone-input-wrapper:focus-within {
    border-color: var(--orange);
    background: white;
    box-shadow: 0 0 0 4px rgba(255, 107, 53, 0.1);
}

.phone-flag-container {
    padding: 0 0.75rem;
    border-right: 1px solid var(--gray-200);
    display: flex;
    align-items: center;
}

.phone-flag {
    font-size: 1.25rem;
}

.phone-input-wrapper input {
    border: none;
    background: transparent;
    padding: 0.9rem 1rem;
}

.phone-input-wrapper input:focus {
    box-shadow: none;
}

.checkbox-group {
    display: flex;
    align-items: flex-start;
    gap: 0.75rem;
    margin-bottom: 1.5rem;
}

.checkbox-group input[type="checkbox"] {
    width: 20px;
    height: 20px;
    accent-color: var(--orange);
    cursor: pointer;
    flex-shrink: 0;
    margin-top: 2px;
}

.checkbox-group label {
    font-size: 0.85rem;
    color: var(--gray-600);
    line-height: 1.5;
}

.checkbox-group a {
    color: var(--orange);
    text-decoration: none;
}

.checkbox-group a:hover {
    text-decoration: underline;
}

.form-success {
    margin-top: 1rem;
    padding: 1rem;
    background: rgba(34, 197, 94, 0.1);
    border-radius: 8px;
    color: #16a34a;
    font-size: 0.9rem;
}

.form-error {
    margin-top: 1rem;
    padding: 1rem;
    background: rgba(239, 68, 68, 0.1);
    border-radius: 8px;
    color: #dc2626;
    font-size: 0.9rem;
}

/* Footer */
footer {
    background: var(--blue-dark);
    color: white;
    padding: 3rem 1.5rem 1.5rem;
}

.footer-content {
    max-width: 1400px;
    margin: 0 auto;
    display: grid;
    grid-template-columns: 2fr 1fr 1fr 1fr;
    gap: 2rem;
    margin-bottom: 2rem;
}

.footer-brand h2 {
    font-size: 1.5rem;
    font-weight: 800;
    margin-bottom: 0.75rem;
    background: linear-gradient(135deg, var(--orange) 0%, var(--orange-light) 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.footer-brand p {
    font-size: 0.9rem;
    opacity: 0.7;
    line-height: 1.7;
    max-width: 300px;
}

.footer-column h4 {
    font-size: 1rem;
    font-weight: 700;
    margin-bottom: 1rem;
    color: white;
}

.footer-column ul {
    list-style: none;
}

.footer-column ul li {
    margin-bottom: 0.5rem;
}

.footer-column ul li a {
    color: rgba(255,255,255,0.7);
    text-decoration: none;
    font-size: 0.9rem;
    transition: all 0.3s ease;
}

.footer-column ul li a:hover {
    color: var(--orange);
    padding-left: 5px;
}

.footer-bottom {
    max-width: 1400px;
    margin: 0 auto;
    padding-top: 1.5rem;
    border-top: 1px solid rgba(255,255,255,0.1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.85rem;
    opacity: 0.7;
}

.footer-bottom a {
    color: white;
    text-decoration: none;
    transition: color 0.3s ease;
}

.footer-bottom a:hover {
    color: var(--orange);
}

/* Back to Top */
.back-to-top {
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    width: 50px;
    height: 50px;
    background: linear-gradient(135deg, var(--orange) 0%, var(--orange-light) 100%);
    color: white;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    font-size: 1.25rem;
    box-shadow: 0 8px 25px rgba(255, 107, 53, 0.4);
    opacity: 0;
    visibility: hidden;
    transform: translateY(20px);
    transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
    z-index: 99;
}

.back-to-top.visible {
    opacity: 1;
    visibility: visible;
    transform: translateY(0);
}

.back-to-top:hover {
    transform: translateY(-5px);
    box-shadow: 0 15px 35px rgba(255, 107, 53, 0.5);
}

/* Responsive */
@media (max-width: 1200px) {
    .benefits-grid,
    .char-grid {
        grid-template-columns: repeat(2, 1fr);
    }
}

@media (max-width: 992px) {
    nav { display: none; }
    .mobile-menu-btn { display: flex; }
    .mobile-nav { display: block; }

    .hero-content {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .hero p { margin: 0 auto 2rem; }
    .hero-buttons { justify-content: center; }
    .hero-visual { display: none; }
    .scroll-indicator { display: none; }

    .about-content,
    .form-wrapper {
        grid-template-columns: 1fr;
    }

    .contacts-grid {
        grid-template-columns: 1fr;
    }

    .footer-content {
        grid-template-columns: 1fr 1fr;
    }

    .header-phones {
        display: none;
    }
}

@media (max-width: 768px) {
    header { padding: 0.75rem 1rem; }

    .hero {
        padding: 3rem 1rem 2rem;
        min-height: auto;
    }

    .hero h2 { font-size: 1.75rem; }

    section { padding: 3rem 1rem; }

    .section-title { font-size: 1.5rem; }

    .category-tabs {
        gap: 0.4rem;
    }

    .category-tab {
        padding: 0.5rem 0.9rem;
        font-size: 0.75rem;
    }

    .products-grid {
        grid-template-columns: repeat(2, 1fr);
        gap: 1rem;
    }

    .product-image { height: 150px; }
    .product-info { padding: 0.9rem; }
    .product-name { font-size: 0.9rem; }
    .product-price { font-size: 1rem; }
    .product-btn { padding: 0.4rem 0.8rem; font-size: 0.7rem; }

    .char-grid,
    .about-features,
    .benefits-grid {
        grid-template-columns: 1fr;
    }

    .contact-card { padding: 1.5rem; }
    .form-container { padding: 1.5rem; }

    .footer-content {
        grid-template-columns: 1fr;
        text-align: center;
    }

    .footer-bottom {
        flex-direction: column;
        text-align: center;
        gap: 0.5rem;
    }

    .back-to-top {
        bottom: 1rem;
        right: 1rem;
        width: 44px;
        height: 44px;
    }
}

@media (max-width: 480px) {
    .logo-img { height: 40px; }
    .logo-text h1 { font-size: 1.2rem; }

    .products-grid {
        grid-template-columns: 1fr;
    }

    .product-image { height: 200px; }

    .btn-large {
        padding: 0.9rem 1.5rem;
        font-size: 0.95rem;
    }

    .category-tab {
        padding: 0.5rem 0.8rem;
        font-size: 0.7rem;
    }
}
